	module.exports = {"data":{"markdownRemark":{"html":"<h2>Array.prototype.splice</h2>\n<p>The  <code>splice()</code> method removes and/or adds elements within an array.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">var</span> myArray <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'a'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'b'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'c'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'d'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\nletters<span class=\"token punctuation\">.</span><span class=\"token function\">splice</span><span class=\"token punctuation\">(</span><span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'e'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// insert 'e' at 2-index</span>\n<span class=\"token comment\" spellcheck=\"true\">// myArray is now [\"a\", \"b\", \"c\", \"d\", \"e\"]</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/splice\">MDN</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-prototype-splice/"},"frontmatter":{"title":"Array.prototype.splice"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-splice/"}}

